<template>
  <div  class="foodMapSection" :style="{'height':innerHeight + 'px','--foodColor':sendColor}">
    <div class="foodMapTitle">
      <div @click="backToHome" class="backBtn">
        <img src="../assets/Map/arrow.svg" alt="arrow">
        <h5>{{sendRoadData.name}}</h5>
      </div>
      <div class="foodName">
        <h1>{{sendName.zh}}</h1>
        <h4>{{sendName.en}}</h4>
      </div>
    </div>
    <div class="foodMapRoadSection" :style="{'height':innerHeight-10 + 'px','--RoadColor':RoadData.MrtColor}">
      <swiper id="scroller" class="mySwiper" :style="{'height':innerHeight - 100 + 'px'}"
          :modules="modules"
          :slidesPerView="7"
          :spaceBetween="40"
          :speed="1500"
          :resistanceRatio="0"
          :free-mode="true"
          :mousewheel="true"
          :observer="true"
          :Virtual="true"
          :centeredSlides="true"
          :slideToClickedSlide="true"
          :direction="'vertical'"
          navigation
          :pagination="{ clickable: true }"
      >
        <swiper-slide  class="slide" v-for="(n, index) in RoadData.station" :key="index" v-slot="{ isActive }">
          {{ isActive ? isRoadActive(n): ''}}
          <div class="roadNameBox">
            <div class="name">{{n.name}}</div>
            <div class="Ename">{{n.ename}}</div>
          </div>
          <div :class="['RoadItemBox',{'RoadItemBoxActive':isActive}]">
            <div class="road">{{RoadData.MrtName}}</div>
            <div class="number">{{n.number}}</div>
          </div>
          <div class="roadItemPoint">
            <div v-if="n.shop" class="shopPoint">
              <span v-if="n.shop.length > 1">{{n.shop.length}}</span>
              <img src="../assets/Map/foodB.svg" alt="foodIcon">
            </div>
            <div v-for="(n, index) in n.other" :key="index" :style="{ borderColor: n.color }" class="shopPoint">
              <h6>{{n.sign}}</h6>
            </div>
          </div>
        </swiper-slide>
      </swiper>
    </div>
    <div class="foodMapFooterSection">
      <div :class="['footerSection',{'open':isOpenList}]">
        <div class="footerTitle">
          <div v-if="notPicRoad" @click="notPicRoad = !notPicRoad" class="roadBtn" :style="{ backgroundColor: RoadData.MrtColor }">
            {{RoadData.MrtName}}
          </div>
          <div v-else class="roadBtnList">
            <div class="roadBtnListItem" @click="picTheRoad(n)" :style="{ backgroundColor: n.MrtColor }" v-for="(n, index) in sendRoadData.road" :key="index">
              {{n.MrtName}}
            </div>
          </div>
          <div class="stationName">
            <div class="name">
              {{isRoadName}}
            </div>
            <div class="ename">
              {{isRoadEname}}
            </div>
          </div>
          <div @click="clickOpenList()" class="footerArrow">
            <img src="../assets/Map/arrow.svg" alt="arrow">
          </div>
        </div>
        <div class="footerList">
          <div v-if="this.isRoadShop" class="slideBox">
            <div v-if="listShow">
              <div class="shopListBox" v-for="(n, index) in isRoadShop" :key="index">
                <div class="listItem">
                  <a class="seeMore" @click="EmbeddingMap('directions','',this.isRoadCoordinate,n.shopID)">
                    <h4>{{n.shopName}}</h4>
                    <span>點我看步行時間</span>
                  </a>
                  <a class="viewLink" @click="EmbeddingMap('place',n.shopID)" href="#">
                    <span>查看資訊</span>
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div v-else class="noData">
            抱歉～暫無資料
          </div>
        </div>
        <div class="footerAD"></div>
      </div>
    </div>
    <div @click="RemoveEmbeddingMap()" id="emberMap" class="EmbeddingMap">
      <div class="mapBox">
        <iframe
            width="100%"
            height="100%"
            style="border:0"
            loading="lazy"
            allowfullscreen
            referrerpolicy="no-referrer-when-downgrade"
            :src="emberSrc">
        </iframe>
      </div>
    </div>
  </div>
</template>

<script>
import { Mousewheel, Virtual ,FreeMode ,Scrollbar} from 'swiper'
import { Swiper, SwiperSlide  } from 'swiper/vue'
import 'swiper/css'
import 'swiper/css/free-mode'
import 'swiper/css/scrollbar'
import {defineComponent} from "vue";
import app from "@/main";
import {getStorage , ref,getDownloadURL} from "firebase/storage";
export default defineComponent({
  name: "FoodMap",
  inheritAttrs: false,
  components: {
    Swiper,
    SwiperSlide,
  },
  props: ['sendRoadData','sendColor','sendName','sendMapData'],
  data() {
    // 資料
    return {
      innerHeight:'',
      isMove:false,
      notPicRoad:true,
      RoadData:[],
      isRoadName:'',
      isRoadEname:'',
      isRoadCoordinate:'',
      isRoadShop:[],
      isOpenList:false,
      listShow:false,
      emberSrc:'',
    };
  },
  watch: {},
  computed: {
  },
  methods: {
    backToHome(){
      this.$emit('backToHome');
    },
    isRoadActive(n){
      this.isRoadName = n.name
      this.isRoadEname = n.ename
      this.isRoadShop = n.shop
      this.isRoadCoordinate = `${n.lat},${n.lng}`
    },
    picTheRoad(n){
      this.RoadData = n
      this.notPicRoad = !this.notPicRoad
    },
    clickOpenList(){
      this.isOpenList = !this.isOpenList
      if(this.isOpenList == false){
        //恢復可以滾動
        function prevent (e) {
          e.preventDefault();
        }
        document.querySelector('#scroller').removeEventListener('touchmove', prevent);
        this.listShow = false
      }else {
        //阻止滾動
        function prevent (e) {
          e.preventDefault();
        }
        document.querySelector('#scroller').addEventListener('touchmove', prevent);
        this.lazyloadList()
      }
    },
    lazyloadList(){
      setTimeout(() => {
        this.listShow = true
      }, 1000);
    },
    replaceImageArray(){
      this.isRoadShop.forEach(function(item){
        // 取得圖片資料庫
        const storage = getStorage(app);
        const starsRef = ref(storage, 'gs://foomap-b6c13.appspot.com/'+item.shopImg);
        getDownloadURL(starsRef)
            .then((url) => {
              item.shopImg = url
            })
      });

    },
    EmbeddingMap(MapType,MapQuite,MapOrigin,MapDestination){
      document.getElementById("emberMap").classList.add("active");
      switch (MapType){
        case 'place':
          this.emberSrc =`https://www.google.com/maps/embed/v1/${MapType}?key=AIzaSyDCRU1ztRD45a59tYjSxl8sdXEFgq1Cw5E&q=place_id:${MapQuite}`
          break
        case 'directions':
          this.emberSrc =`https://www.google.com/maps/embed/v1/${MapType}?key=AIzaSyDCRU1ztRD45a59tYjSxl8sdXEFgq1Cw5E&origin=${MapOrigin}&destination=place_id:${MapDestination}&avoid=tolls&mode=walking`
          break
      }
    },
    RemoveEmbeddingMap(){
      document.getElementById("emberMap").classList.remove("active");
      this.emberSrc = ''
    },
  },
  setup() {
    return {
      modules: [Mousewheel,Virtual,FreeMode,Scrollbar],
    };
  },
  mounted(){
    this.innerHeight = window.innerHeight;
    //預設使用第一條路線
    this.RoadData = this.sendRoadData.road[0];
    //阻止滾動
    function prevent (e) {
      e.preventDefault();
    }
    document.querySelector('#scroller').addEventListener('touchmove', prevent);


  },
})
</script>

<style scoped lang="scss">
@import "src/style/mainStyle";
</style>