<template >
  <div :class="['homePage innerHeight',{'open':intoMap}]" :style="{'height':innerHeight + 'px','--foodColor':isActiveFoodColor}">
    <div :class="['homePageSwiper']" :style="{'--foodColor':isActiveFoodColor}">
      <div class="titleBox">
        <div class="logoBox">
          <h1>Hello FooMap</h1>
          <span>the Food Map Recommendations</span>
        </div>
        <div class="langBox">
          <div class="lan">zh</div>
          <div class="cy">TW</div>
        </div>
      </div>
      <swiper class="swiper" :modules="modules" :pagination="{ clickable: true,renderBullet: bulletRenderer }">
        <swiper-slide :class="['slide',{'swiper-no-swiping':intoMap}]" v-for="(n, index) in getAllData" :key="index" v-slot="{ isActive }">
          <img :src="n.foodImg" alt="food" :style="{'--innerHeight':innerHeight}">
          {{ isActive ? isActiveFood(n) : '' }}
        </swiper-slide>
      </swiper>
    </div>
    <div @click="findFood()" class="homeBack">
      <img src="../assets/Map/arrow.svg" alt="arrow">
    </div>
    <div @click="findFood()" :class="['findFoodBtn']" :style="{'--foodColor':isActiveFoodColor}">
      Find the Map
    </div>
    <div class="foodName" :style="{'--foodColor':isActiveFoodColor}">
      <div @click="inToAdmin()" class="foodName_box">
        <h1>{{isActiveFoodName.zh}}</h1>
        <span>{{isActiveFoodName.en}}</span>
      </div>
    </div>
    <div class="foodMapSection" :style="{'--foodColor':isActiveFoodColor}"  >
      <swiper
          class="swiper"
          :modules="modules"
          :space-between="100"
          :slides-per-view="3"
          :centered-slides="true"
          :loop="true"
      >
        <swiper-slide class="slide" v-for="(n, index) in isActiveFoodMap" :key="index" v-slot="{ isActive }">
          {{ isActive ? isActiveRoad(n) : '' }}
          <div @click="postActiveData()" class="roadBox">
            <img :src="n.img" alt="road">
            <h4>{{n.name}}</h4>
          </div>
        </swiper-slide>
      </swiper>
    </div>
  </div>
  <div v-if="inToadminData === 10" class="alertBox">
    <div class="title">警告</div>
    <div>即將進入後台修改位置</div>
    <div class="centerBox">
      <div class="btnA" @click="backAndToAdmin">確定進入</div>
      <div class="btnB" @click="inToAdmin">按錯了</div>
    </div>
  </div>
</template>

<script>
import { Pagination } from 'swiper'
import { Swiper, SwiperSlide  } from 'swiper/vue'
import 'swiper/css'
import 'swiper/css/pagination'
import {defineComponent} from "vue";
export default defineComponent({
  name: "HomePage",
  inheritAttrs: false,
  components: {
    Swiper,
    SwiperSlide,
  },
  props: ['getAllData'],
  data() {
    // 資料
    return {
      innerHeight:'',
      intoMap:false,
      foodTouch:true,
      isActiveFoodColor:'',
      isActiveFoodName:[],
      isActiveFoodMap:[],
      isActiveMapRoad:[],
      colorArray:'',
      inToadminData:0,
    };
  },
  watch: {},
  computed: {
  },
  methods: {
    isActiveFood(n){
      //選定食物顏色
      this.isActiveFoodColor = n.foodColor
      //選定食物名稱
      this.isActiveFoodName = n.foodName
      //選定食物地圖
      this.isActiveFoodMap = n.section
      //傳出顏色陣列
      this.mapTheColor
    },
    isActiveRoad(n){
      //選定食物路線
      this.isActiveMapRoad = n
    },
    findFood(){
      if(this.intoMap == false){
        this.intoMap = true
        this.foodTouch = false
      }else {
        this.intoMap = false
        this.foodTouch = true
      }
    },
    mapTheColor(){
      this.colorArray = this.getAllData.map(item => item.foodColor);
      // console.log('colorArray',this.colorArray)
    },
    postActiveData(){
      //送出選定的資料至app.vue
      this.$emit('sendActiveData',this.isActiveMapRoad,this.isActiveFoodColor,this.isActiveFoodName,this.isActiveFoodMap);
    },
    inToAdmin(){
      this.inToadminData ++
      // console.log('inToadminData',this.inToadminData)
      if( this.inToadminData == 11){
        this.inToadminData = 0
      }
    },
    backAndToAdmin(){
      this.inToadminData = 0
      this.$emit('goToAdmin')
    },
  },
  setup() {
    const bulletRenderer = (index, className) => {
      const _className = `${className} bullet-custom-${index + 1}`
      return `<span class="${_className}"></span>`
    }
    return {
      bulletRenderer,
      modules: [Pagination]
    };
  },
  mounted(){
    this.innerHeight = window.innerHeight;
    console.log('innerHeight',this.innerHeight)
  },
})
</script>

<style scoped lang="scss">
  @import "src/style/mainStyle";
</style>