<template>
  <div  class="mobile">
    <HomePage v-if="isHomePage" :getAllData="AllData" @sendActiveData="takeActiveData" @goToAdmin="AdminBase"></HomePage>
    <adminPage v-else-if="isAdmin" @goToHome="goToHome"></adminPage>
    <FoodMap v-else @backToHome="backToHome()" :sendRoadData="picMapRoad" :sendColor="picFoodColor" :sendName="picFoodName" :sendMapData="picFoodMap"></FoodMap>
  </div>
</template>

<script>
import HomePage from './components/home.vue'
import FoodMap from './components/foodmap.vue'
import adminPage from './components/admin.vue'
import {app} from "@/main";
import {child, get, getDatabase, ref} from "firebase/database";
export default {
  name: 'App',
  components: {
    HomePage,
    FoodMap,
    adminPage,
  },
  mixins:[],
  data() {
    // 資料
    return {
      AllData:[],
      picMapRoad:[],
      picFoodColor:'',
      picFoodName:[],
      picFoodMap:[],
      isHomePage:true,
      isAdmin:false,
    };
  },
  watch: {
    //監聽值
  },
  computed: {
    //相依的資料改變時才做計算方法
  },
  methods: {
    takeActiveData(picMapRoad,picFoodColor,picFoodName,picFoodMap){
      //將home傳回來的值儲存於app的data中
      this.picMapRoad = picMapRoad //選擇的地圖
      this.picFoodColor = picFoodColor //選擇的食物顏色
      this.picFoodName = picFoodName //選擇的食物名稱
      this.picFoodMap = picFoodMap //選擇的食物地圖

      this.isHomePage = false //切換分頁
    },
    backToHome(){
      this.isHomePage = true
    },
    AdminBase(){
      this.isHomePage = false
      this.isAdmin = true
    },
    goToHome(){
      this.isHomePage = true
      this.isAdmin = false
    }

  },
  //BEGIN--生命週期
  beforeCreate: function() {
    //實體初始化
  },
  created: function() {
    //實體建立完成。資料 $data 已可取得，但 $el 屬性還未被建立。
  },
  beforeMount: function() {
    //執行元素掛載之前。
  },
  mounted: function() {
    //元素已掛載， $el 被建立。
    // 鎖定右鍵
    document.oncontextmenu = new Function("return false");
    oncontextmenu="return false;"

    //連結資料庫
    const dbRef = ref(getDatabase(app));
    get(child(dbRef, `/`)).then((snapshot) => {
      if (snapshot.exists()) {
        this.AllData = snapshot.val().mapAPIData
        // console.log('allData',this.AllData)
      } else {
        console.log("No data available");
      }
    }).catch((error) => {
      console.error(error);
    });


  },
  beforeUpdate: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
  updated: function() {
    //當資料變化時被呼叫，還不會描繪 View。
  },
//END--生命週期
}
</script>

<style lang="scss">
  @import "src/style/mainStyle";
  body{
    margin: 0;
  }
</style>
