<template>
  <div class="adminSection">
    <div v-if="adminPage == 0" class="login">
      <h2>管理者中心</h2>
      <div class="LoginBtn" @click="googleLogin">
        使用Google登入
      </div>
      <div class="LoginBtn" @click="facebookLogin">
        使用Facebook登入
      </div>
      <div class="LoginBtn" @click="back">
        退出管理者中心
      </div>
    </div>
    <div v-else-if="adminPage == 1"  class="viewData">
      <h2>管理者中心</h2>
      <h3>歡迎: {{userData[1].user.displayName}} 登入本系統</h3>
      <h4>{{userData[1].user.email}}</h4>
      <div class="console">
        <div :class="['btn',{'active':pageView === 0}]" @click="pageView = 0">查看資料</div>
<!--        <div :class="['btn',{'active':pageView === 1}]" @click="pageView = 1">更新商店資料</div>-->
        <div :class="['btn',{'active':pageView === 3}]" @click="pageView = 3">API更新資料</div>
        <div :class="['btn',{'active':pageView === 2}]" @click="pageView = 2">資料貢獻清冊</div>
        <div :class="['btn',{}]" @click="loginOut()">登出並離開</div>
      </div>
      <div v-if="pageView == 0" class="viewData">
        <json-viewer
            :value="mapData"
            :expand-depth=5
            boxed
            sort></json-viewer>
      </div>
      <div v-else-if="pageView == 1" class="viewData">
        <p class="updataText">更新商店資料請選擇食物->選擇地圖->選擇路線->選擇站點後輸入您需要更新的商店資料</p>
        <div class="form">
          <div class="inputBox">
            <p>選擇更新的食物為:</p>
            <select v-model="checkedFood" @change="this.pathFood = $event.target.selectedIndex">
              <option v-for="(n, index) in mapData" :key="index" :value="n">
                {{ n.foodName.zh }}
              </option>
            </select>
          </div>
          <div v-show="checkedFood != '' " class="inputBox">
            <p>選擇更新的地圖為:</p>
            <select v-model="checkedSection" @change="this.pathSection = $event.target.selectedIndex">
              <option v-for="(n, index) in checkedFood.section" :key="index" :value="n">
                {{ n.name }}
              </option>
            </select>
          </div>
          <div v-show="checkedSection != '' " class="inputBox">
            <p>選擇更新的路線為:</p>
            <select v-model="checkedRoad" @change="this.pathRoad = $event.target.selectedIndex">
              <option v-for="(n, index) in checkedSection.road" :key="index" :value="n">
                {{ n.MrtName }}
              </option>
            </select>
          </div>
          <div v-show="checkedRoad != '' " class="inputBox">
            <p>選擇更新的站點為:</p>
            <select v-model="checkedStation" @change="this.pathStation = $event.target.selectedIndex">
              <option v-for="(n, index) in checkedRoad.station" :key="index" :value="n">
                {{ n.name }}
              </option>
            </select>
          </div>

          <div class="inputBox input">
            <input v-model="shopName" placeholder="請輸入店名" @blur="notInput('shopName')">
            <p>新增商店名稱為: {{ shopName }}</p>
          </div>
          <div class="inputBox input">
            <input type="number" v-model="shopKm" placeholder="請輸入公里" @blur="notInput('shopKm')">
            <p>商店距離距站點為: {{ shopKm }} 公里</p>
          </div>
          <div class="inputBox input">
            <textarea v-model="shopLink" @blur="notInput('shopLink')" placeholder="請貼上google地圖連結" />
            <p>新增商店Google Map連結:<br> {{ shopLink }}</p>
          </div>
          <div class="inputBox checkbox">
            <input type="checkbox" @change="finalCheck()" id="checkbox" v-model="checked">
            <label for="checkbox">確認資料無誤</label>
          </div>
          <div class="errorMsg">{{errMsg}}</div>
          <div class="inputBox btnBox">
            <div @click="sendData" :class="['btn-11',{'disabled':!checked}]">送出更新商店</div>
          </div>

        </div>
      </div>
      <div v-else-if="pageView == 2" class="viewData">
        <json-viewer
            :value="mapDataList"
            :expand-depth=5
            boxed
            sort></json-viewer>
      </div>
      <div v-else-if="pageView == 3" class="viewData">
        <p class="updataText">更新商店資料請選擇食物->選擇地圖->選擇路線->選擇站點</p>
        <div class="form">
          <div class="inputBox">
            <p>選擇更新的食物為:</p>
            <select v-model="checkedFood" @change="this.pathFood = $event.target.selectedIndex">
              <option v-for="(n, index) in mapData" :key="index" :value="n">
                {{ n.foodName.zh }}
              </option>
            </select>
          </div>
          <div v-show="checkedFood != '' " class="inputBox">
            <p>選擇更新的地圖為:</p>
            <select v-model="checkedSection" @change="this.pathSection = $event.target.selectedIndex">
              <option v-for="(n, index) in checkedFood.section" :key="index" :value="n">
                {{ n.name }}
              </option>
            </select>
          </div>
          <div v-show="checkedSection != '' " class="inputBox">
            <p>選擇更新的路線為:</p>
            <select v-model="checkedRoad" @change="this.pathRoad = $event.target.selectedIndex">
              <option v-for="(n, index) in checkedSection.road" :key="index" :value="n">
                {{ n.MrtName }}
              </option>
            </select>
          </div>
          <div v-show="checkedRoad != '' " class="inputBox">
            <p>選擇更新的站點為:</p>
            <select v-model="checkedStation" @change="this.pathStation = $event.target.selectedIndex">
              <option v-for="(n, index) in checkedRoad.station" :key="index" :value="n">
                {{ n.name }}
              </option>
            </select>
          </div>
          <div class="inputBox checkbox">
            <input type="checkbox" @change="APIitemCheck()" id="checkbox2" v-model="checked2">
            <label for="checkbox2">已確定食物及站點選擇</label>
          </div>
          <div class="APIbtnGroup">
            <div :class="['APIsendBtn',{'active':isNotGetData}]" @click="findShopButtonPressed">
              使用google API新增商店
            </div>
            <!--            <div class="APIsendBtn" @click="DetailsButtonPressed">-->
            <!--              查看商店細節-->
            <!--            </div>-->
          </div>
          <div class="APIdataList">
            <div class="dataItem" v-for="(n, index) in placesData" :key="index">
              <div>商店名稱:{{ n.name }}</div>
              <div>placeID:{{ n.place_id }}</div>
            </div>
          </div>
          <div class="APIbtnGroup">
            <div :class="['APIsendBtn',{'active':isMoredData}]" @click="NextPageButtonPressed">
              載入更多資料
            </div>
          </div>
          <div class="inputBox checkbox">
            <input type="checkbox" @change="APIfinalCheck()" id="checkbox" v-model="checked">
            <label for="checkbox">確認資料無誤</label>
          </div>
          <div class="errorMsg">{{errMsg}}</div>
          <div class="inputBox btnBox">
            <div @click="sendAPIData" :class="['btn-11',{'disabled':!checked}]">送出更新商店</div>
          </div>

        </div>
      </div>
    </div>
    <div :class="['popup',{'active':popup}]">
      <h3>{{popMsg}}</h3>
    </div>
  </div>
</template>

<script>
import {getDatabase, ref , onValue, child , push, update} from "firebase/database";
import {app} from "@/main";
import JsonViewer from 'vue-json-viewer'
import { getAuth, signInWithPopup, GoogleAuthProvider, FacebookAuthProvider } from "firebase/auth";
import axios from "axios";

export default {
  name: "adminPage",
  components: {
    JsonViewer,
  },
  data() {
    // 資料
    return {
      mapData:[],
      mapDataList:[],
      pageView:0,
      shopName:'',
      shopKm:'',
      shopLink:'',
      shopID:'',
      checked:'',
      checked2:'',
      //選擇食物
      checkedFood:[],
      pathFood:'',
      //選擇地圖
      checkedSection:[],
      pathSection:'',
      //選擇路線
      checkedRoad:[],
      pathRoad:'',
      //選擇站點
      checkedStation:[],
      pathStation:'',
      adminPage:0,
      userData:[],
      errorUserData:[],
      errMsg:'',
      popup:false,
      popMsg:'',
      lat: '',
      lng: '',
      type: "restaurant",
      keyword:"",
      radius: "0.7",
      places: [],
      placesData: [],
      isNotGetData:false,
      isMoredData:false,
    }
  },
  watch: {
    //監聽值
    errMsg(newPic) {
      if(newPic !== ''){
        this.checked = false
      }
    }
  },
  methods: {
    back(){
      this.$emit('goToHome')
      this.userData = []
      this.adminPage = 0
    },
    loginOut(){
      this.adminPage = 0
      this.userData = []
    },
    upDataTheMap(shopName,shopKm,shopLink,pathFood,pathSection,pathRoad,pathStation,pathIndex){
      const db = getDatabase(app);
      // A post entry.
      const postData = {
        shopName: shopName,
        shopKm: shopKm,
        shopLink: shopLink,
      };
      // Get a key for a new Post.
      // const newPostKey = push(child(ref(db), 'posts')).key;

      // Write the new post's data simultaneously in the posts list and the user's post list.
      const updates = {};
      updates[ '/mapData/'+ pathFood + '/section/' + pathSection + '/road/' + pathRoad + '/station/' + pathStation + '/shop/' + pathIndex] = postData;
      return update(ref(db), updates);
    },
    upDataAPIMap(pathFood,pathSection,pathRoad,pathStation,shopName,shopId,shopIndex){
        const db = getDatabase(app);
        const postData = {
          shopName: shopName,
          shopID: shopId,
        };
        const updates = {};
        updates[ '/mapAPIData/'+ pathFood + '/section/' + pathSection + '/road/' + pathRoad + '/station/' + pathStation + '/shop/' + shopIndex] = postData;
        return update(ref(db), updates);
    },
    upDataTheList(shopName,shopKm,shopLink,pathFood,pathSection,pathRoad,pathStation,pathIndex){
      const db = getDatabase(app);
      const postData = {
        Updater:this.userData[1].user.displayName,
        UpdatePersonMail:this.userData[1].user.email,
        UpdateTime:this.userData[1].user.metadata.lastSignInTime,
        shopName: shopName,
        shopKm: shopKm,
        shopLink: shopLink,
        shopPath: '/mapData/'+ pathFood + '/section/' + pathSection + '/road/' + pathRoad + '/station/' + pathStation + '/shop/' + pathIndex,
      };
      // Get a key for a new Post.
      const newPostKey = push(child(ref(db), 'posts')).key;

      // Write the new post's data simultaneously in the posts list and the user's post list.
      const updates = {};
      updates[ '/Update-data-list/'+ this.userData[1].user.uid + '/' + newPostKey] = postData;
      return update(ref(db), updates);
    },
    sendData() {
      //呼叫更新資料方法
      if(this.checked === true){
        //更新至最新的位置
        let pathIndex = this.checkedStation.shop.length
        this.upDataTheMap(this.shopName,this.shopKm,this.shopLink,this.pathFood,this.pathSection,this.pathRoad,this.pathStation,pathIndex)
        this.upDataTheList(this.shopName,this.shopKm,this.shopLink,this.pathFood,this.pathSection,this.pathRoad,this.pathStation,pathIndex)
        //清空當前資料
        this.shopName = ''
        this.shopKm = ''
        this.shopLink = ''
        this.checked = false
        //打開提示彈窗
        this.setPopup("1000")
        this.popMsg = '感謝您的資料提供！'
      }
      // console.log('userData',this.userData)
    },
    sendAPIData() {
      //呼叫更新資料方法
      if(this.checked === true){

        const waitFor = (ms) => new Promise(r => setTimeout(r, ms));
        this.placesData.forEach(async (n,index) => {
          await waitFor(50);
          this.upDataAPIMap(this.pathFood,this.pathSection,this.pathRoad,this.pathStation,n.name,n.place_id,index)
        });


        //清空當前資料
        this.checked = false
        //打開提示彈窗
        this.setPopup("1000")
        this.popMsg = '感謝您的資料提供！'
      }
      // console.log('userData',this.userData)
    },
    googleLogin(){
      //連結驗證程序
      const provider = new GoogleAuthProvider();
      const auth = getAuth(app);
      signInWithPopup(auth, provider)
          .then((result) => {
            // This gives you a Google Access Token. You can use it to access the Google API.
            const credential = GoogleAuthProvider.credentialFromResult(result);
            const token = credential.accessToken;
            // The signed-in user info.
            const user = result.user;
            //儲存使用者資料
            this.userData = [
              {'token':token},
              {'user':user},
            ]
            this.adminPage = 1
          }).catch((error) => {
        // Handle Errors here.
        const errorCode = error.code;
        const errorMessage = error.message;
        // The email of the user's account used.
        const email = error.customData.email;
        // The AuthCredential type that was used.
        const credential = GoogleAuthProvider.credentialFromError(error);

        this.errorUserData = [
          {'errorCode':errorCode},
          {'errorMessage':errorMessage},
          {'email':email},
          {'credential':credential},
        ]
        this.setPopup("3000")
        this.popMsg = '您的帳號似乎無法使用！' + this.errorUserData[1].errorMessage
      });
    },
    facebookLogin(){
      //連結驗證程序
      const provider = new FacebookAuthProvider();
      const auth = getAuth(app);
      signInWithPopup(auth, provider)
          .then((result) => {
            // This gives you a Google Access Token. You can use it to access the Google API.
            const credential = FacebookAuthProvider.credentialFromResult(result);
            const token = credential.accessToken;
            // The signed-in user info.
            const user = result.user;
            //儲存使用者資料
            this.userData = [
              {'token':token},
              {'user':user},
            ]
            this.adminPage = 1
          })
        .catch((error) => {
        // Handle Errors here.
        const errorCode = error.code;
        const errorMessage = error.message;
        // The email of the user's account used.
        const email = error.customData.email;
        // The AuthCredential type that was used.
        const credential = FacebookAuthProvider.credentialFromError(error);

        this.errorUserData = [
          {'errorCode':errorCode},
          {'errorMessage':errorMessage},
          {'email':email},
          {'credential':credential},
        ]
          this.setPopup("3000")
          this.popMsg = '您的帳號似乎無法使用！' + this.errorUserData[1].errorMessage
      });
    },
    //判斷是否有正確輸入
    notInput(item){
      switch (item){
        case 'shopName':
          if(this.shopName === ''){
            this.errMsg = '沒有輸入名稱'
          }else {
            this.errMsg = ''
          }
          break;
        case 'shopKm':
          if(this.shopKm === ''){
            this.errMsg = '沒有輸入公里'
          }else {
            this.errMsg = ''
          }
          break;
        case 'shopLink':
          if(this.shopLink === ''){
            this.errMsg = '沒有輸入連結'
          }else {
            this.errMsg = ''
          }
          break;

      }
    },
    //確定路線溝溝
    APIitemCheck(){
      if(this.pathFood === '' || this.pathSection === '' || this.pathRoad === '' || this.pathStation === ''
      ){
        this.checked2 = false
        this.errMsg = '選項沒有填寫完畢'
      }else {
        this.errMsg = ''
        this.isNotGetData = true
        this.keyword = this.checkedFood.keyWord
        this.lat = this.checkedStation.lat
        this.lng = this.checkedStation.lng
      }
    },
    //最終確認勾勾
    finalCheck(){
      if(this.pathFood === '' || this.pathSection === '' || this.pathRoad === '' || this.pathStation === '' || this.shopName === '' || this.shopKm === '' || this.shopLink === ''
      ){
        this.checked = false
        this.errMsg = '選項沒有填寫完畢'
      }
    },
    APIfinalCheck(){
      if(this.placesData.length === 0 ) {
        this.errMsg = '資料尚未生成'
        this.checked = false
      }else {
        this.errMsg = ''
        this.checked = true
      }
    },
    //更新資料彈窗
    setPopup(time){
      this.popup = true
      setTimeout(() => {
        this.popup = false
        this.popMsg = ''
      }, time)
    },
    findShopButtonPressed(){
      if(this.checked2 === false) return
      this.isNotGetData = false
      const URL = `https://maps.googleapis.com/maps/api/place/nearbysearch/json?location=
      ${this.lat}, ${this.lng}&type=${this.type}&radius=${this.radius * 1000}&keyword=${this.keyword}&key=AIzaSyC5uof5o5rNtVVCbtL-iBPg03piPrOw5Xw&language=zh-TW`;
      axios.get(URL).then(response => {
        this.places = response.data;
        this.placesData = response.data.results;
        if(this.places.next_page_token !== undefined)
        {this.isMoredData = true}
      }).catch(error => {
        console.log(error.message);
      });
    },
    NextPageButtonPressed(){
      if(this.places.next_page_token !== undefined){
        const URL = `https://maps.googleapis.com/maps/api/place/nearbysearch/json?pagetoken=${this.places.next_page_token}&key=AIzaSyC5uof5o5rNtVVCbtL-iBPg03piPrOw5Xw`;
        axios.get(URL).then(response => {
          this.places = response.data;
          this.placesData = this.placesData.concat(response.data.results);
          if(this.places.next_page_token === undefined)
          {this.isMoredData = false}
        }).catch(error => {
          console.log(error.message);
        });
      }
    },
    DetailsButtonPressed(){
      const URL = `https://cors-anywhere.herokuapp.com/https://maps.googleapis.com/maps/api/place/details/json?place_id=ChIJPxnPnrCrQjQRmEwUNrIViu0&language=zh-TW&key=AIzaSyCHb6eKPBxhkTcIfsoPu4RoTPw64gTVLDQ`;
      axios.get(URL).then(response => {
        this.places = response.data;
        console.log('Details',this.places)
      }).catch(error => {
        console.log(error.message);
      });
    },
  },
  mounted: function() {
    //連結資料庫
    const db = getDatabase(app);
    const starCountRef = ref(db, '/mapAPIData');
    onValue(starCountRef, (snapshot) => {
      const data = snapshot.val();
      this.mapData = data
    }, {
      onlyOnce: false
    });
    const starCountListRef = ref(db, '/Update-data-list');
    onValue(starCountListRef, (snapshot) => {
      const data = snapshot.val();
      this.mapDataList = data
    }, {
      onlyOnce: false
    });
  }
}
</script>

<style lang="scss" scoped>
  @import "src/style/mainStyle";
</style>